import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UserService } from '../../shared/index';
import { AuthPage } from '../auth/auth';
import { SettingProvider } from '../../providers/setting/setting';

/**
 * Generated class for the ClassResourcePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-class-resource',
  templateUrl: 'class-resource.html',
})
export class ClassResourcePage extends AuthPage{

  header_title:any;

  constructor(public navCtrl: NavController, public navParams: NavParams, protected userService : UserService) {
    super(navCtrl,navParams,userService)
    this.header_title = SettingProvider.pageSettings.ClassResourcePage.pageTitle
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ClassResourcePage');
  }

}



